import { internalFetch } from '@/util/fetch';
import { stringifPath } from './index';
const refundListPATH = '/manageTaskOrder/findRefundTaskList';
const orderListPath = '/manageTaskOrder/findTaskOrderList';
const freeListPath = '/manageTaskOrder/findFreeTaskList';
const auditFreeOrderPath = '/manageTaskOrder/auditFreeTaskOrder';
const pagetotalPath = '/manageTaskOrder/findTableCount';

/* */
export function refundOrderList(page: string) {
  return internalFetch('POST')(true)(stringifPath(refundListPATH), {
    body: { page, page_size: 20 }
  });
}
export interface orderListData {
  id: string;
  task_no: string;
  account: string;
  order_no: string;
  check_first_url: string;
  check_second_url: string;
  status: string;
  order_pic_url: string;
  wechat_code_url: string;
  is_refund: string;
  title: string;
  gift: string;
  content: string;
  extra_gift: string;
  create_time: string;
  refund: string;
  phone: string;
}

export interface freeListData {
  id: string;
  title: string;
  amount: string;
  refund_rate: string;
  url: string;
  total: string;
  execute_time: string;
  gift: string;
  extra_gift: string;
  total_count: string;
  receive_count: string;
  finish_count: string;
  refund: string;
}
export function getOrderList(page: string, task_id: string, order_no: string, phone: string, account: string) {
  return internalFetch('POST')(true)(stringifPath(orderListPath), {
    body: { page, page_size: 20, task_id, order_no, phone, account }
  });
}
export function getFreeList(page: string) {
  return internalFetch('POST')(true)(stringifPath(freeListPath), {
    body: { page, page_size: 20 }
  });
}
export function getPageTotal(type: string) {
  return internalFetch('POST')(true)(stringifPath(pagetotalPath), {
    body: { type }
  });
}
export function setAuditFreeOrder(taskOrderId: string, status: string) {
  return internalFetch('POST')(true)(stringifPath(auditFreeOrderPath), {
    body: { taskOrderId, status }
  });
}
/**/
// manageTaskOrderDetail/getFreeTaskOrderCount 
// manageTaskOrderDetail/findFreeTaskOrderDetailPage  :page  page_size
//    account COMMENT '',
//   `check_first_url` varchar(1024) NOT NULL DEFAULT '' COMMENT 'URL',
//   `check_second_url` varchar(1024) NOT NULL DEFAULT '' COMMENT 'URL',
//   `task_id` int(11) NOT NULL DEFAULT '0' COMMENT 'ID',
//   `task_no` varchar(64) NOT NULL DEFAULT '' COMMENT '',
//   `url` varchar(1024) NOT NULL DEFAULT '' COMMENT 'URL',
//   `amount` int(11) NOT NULL DEFAULT '0' COMMENT '',
//   `order_no` varchar(64) NOT NULL DEFAULT '' COMMENT '',
//   `refund` decimal(14,2) NOT NULL DEFAULT '0.00' COMMENT '',
//   `bonus_point` int(11) NOT NULL DEFAULT '0' COMMENT '',
//   `execute_time` date NOT NULL COMMENT '',
//   `order_pic_url` varchar(1024) NOT NULL DEFAULT '' COMMENT 'URL',
//   `wechat_code_url` varchar(1024) NOT NULL DEFAULT '' COMMENT '',
//   `status` smallint(1) NOT NULL DEFAULT '0' COMMENT ' 0 1 2 3 4 5 ',
//   `title` varchar(128) NOT NULL DEFAULT '' COMMENT '',
//   `gift` varchar(1024) NOT NULL DEFAULT '' COMMENT '',
//   `content` varchar(1024) NOT NULL DEFAULT '' COMMENT '',
//   `create_time` datetime NOT NULL DEFAULT CURRENT_TIMESTAMP COMMENT ''
export interface TaskOrderDetailData {
  account: string;
  check_first_url: string;
  check_second_url: string;
  task_id: string;
  task_no: string;
  url: string;
  amount: string;
  order_no: string;
  refund: string;
  bonus_point: string;
  execute_time: string;
  order_pic_url: string;
  wechat_code_url: string;
  status: string;
  title: string;
  gift: string;
  content: string;
  extra_gift: string;
  create_time: string;
  phone: string;
}
const getFreeTaskOrderCountPath = '/manageTaskOrderDetail/getFreeTaskOrderCount';
const TaskOrderDetailPath = '/manageTaskOrderDetail/findFreeTaskOrderDetailPage';
export function TaskOrderDetail(page: string, task_id: string, order_no: string, phone: string, account: string) {
  return internalFetch('POST')(true)(stringifPath(TaskOrderDetailPath), {
    body: { page, page_size: 20, task_id, order_no, phone, account }
  });
}
export function getFreeTaskOrderCount() {
  return internalFetch('POST')(true)(stringifPath(getFreeTaskOrderCountPath));
}
// 
// manageTaskOrderDetail/getRefundTaskOrderCount 
// manageTaskOrderDetail/findRefundTaskOrderDetailPage  :page  page_size

//   `title` varchar(256) NOT NULL DEFAULT '' COMMENT '',
//   `task_no` varchar(64) NOT NULL DEFAULT '' COMMENT '',
//   `url` varchar(1024) NOT NULL DEFAULT '' COMMENT 'URL',
//   `refund` int(11) NOT NULL DEFAULT '0' COMMENT '',
//   `content` varchar(512) NOT NULL DEFAULT '' COMMENT '',
//   `status` smallint(1) NOT NULL DEFAULT '0' COMMENT '01 2',
//   `bonus_point` int(11) NOT NULL DEFAULT '0' COMMENT '',
//    account 
//    square_url 
//    long_url   
//    keyword  
//   `execute_time` date NOT NULL COMMENT '',
//   `create_time` datetime NOT NULL DEFAULT CURRENT_TIMESTAMP COMMENT ''
export interface RefundTaskOrderDetailData {
  account: string;
  square_url: string;
  long_url: string;
  task_id: string;
  task_no: string;
  url: string;
  amount: string;
  order_no: string;
  refund: string;
  bonus_point: string;
  execute_time: string;
  status: string;
  keyword: string;
}
const getRefundTaskOrderCountPath = '/manageTaskOrderDetail/getRefundTaskOrderCount';
const RefundTaskOrderDetailPage = '/manageTaskOrderDetail/findRefundTaskOrderDetailPage';
export function RefundTaskOrderDetail(page: string) {
  return internalFetch('POST')(true)(stringifPath(RefundTaskOrderDetailPage), {
    body: { page, page_size: 20 }
  });
}
export function getRefundTaskOrderCount() {
  return internalFetch('POST')(true)(stringifPath(getRefundTaskOrderCountPath));
}



